const CheckboxRow = styled.div`
    margin-bottom: 15px;
`

const Info = styled.div`
    margin-bottom: 6rem;

    .title{
        height: 4rem;
        font-size: 3.4rem;
        font-family: Brandon;
        font-weight: 300;
        color: #10134E;
        line-height: 4rem;
        margin-bottom: 0.9rem;
    }
    .detail{
        width: 60rem;
        font-size: 1.8rem;
        font-family: Brandon;
        font-weight: 300;
        color: #A6A6B7;
        line-height: 2.2rem;
    }
`

export default function SecondStep(props) {
    const { state, dispatch } = useWeb3();
    const { maincontract, Account, web3Instance, web3InstanceState } = state;

    let [selected, setselected] = useState(0);
    
    const [newDaoId, setNewDaoId] = useState(null);

    const [logo, setlogo] = useState('');
    const [daoname, setdaoname] = useState('');
    const [desc, setdesc] = useState('');


    const components = [
        {
            name: "AUTH",
            value: "AuthManager",
            img: AUTH
        },
        {
            name: "VOTE",
            value: "VoteManager",
            img: VOTE
        },
        {
            name: "VAULT",
            value: "VaultManager",
            img: VENTURE
        },
        {
            name: "ORG",
            value: "OrgManager",
            img: ORG
        }
    ]
    let [list] = useState([
        {
            id: 0,
            name: "DAO",
            components: components
        },
        {
            id: 1,
            name: "DAO Venture",
            components: components.concat([{
                name: "VENTURE",
                vault: "",
                img: VENTURE,
            }])
        },
    ]);

    const [checkedList, setCheckedList] = useState(components.map(c=>c.value));
    const [chooseMap, setChooseMap] = useState({'AuthManager': true})

    let { t } = useTranslation();

    const toThirdStep = () => {
        props.handleTemplate(selected)
        props.handlerSet(3);
        sessionStorage.setItem('secondStep', JSON.stringify(checkedList))
    }
    const toFirstStep = () => {
        props.handlerSet(1)
    }
    const handleSelect = (e) => {
        let id = Number(e.currentTarget.id);
        let template = list.filter(item => item.id === id);
        setselected(template[0].id)
    }

    const switchImg =(namestr)=>{
        let img = '';
        switch (namestr.toUpperCase()) {
            case 'BASE':
                img = BASE;
                break;
            case 'ERC20':
                img = ERC20;
                break;
            case 'ORG':
                img = ORG;
                break;
            case 'VAULT':
                img = VAULT;
                break;
            case 'VOTE':
                img =  VOTE;
                break;
            case 'AUTH':
                img = AUTH;
                break;
            default:
                img ='';
                break;
        }
        return img
    }

    useEffect(() => {

        // let account = JSON.parse(sessionStorage.getItem('account'));

        if (web3InstanceState !== 'READY') return;
        dispatch({ type: 'LOAD_MAINCONTRACT' });

    }, [web3InstanceState]);
    useEffect(() => {
        if (maincontract == null) return;
        const setlisttemplate = async () => {

            // await api.main.listTemplates(maincontract).then(data => {
            //     if (!data) {
            //         dispatch({ type: 'LOADINGTYPE', payload: t('initializationFailed') });
            //
            //         setTimeout(() => {
            //             props.history.push(`/`)
            //         }, 2000)
            //     } else {
            //         setlist(data);
            //
            //         dispatch({ type: 'LOADINGTYPE', payload: null });
            //     }
            // })
        };
        setlisttemplate();
    }, [maincontract]);

    useEffect(() => {
        const d = {}
        checkedList.forEach(k=>{ d[k] = true })
        setChooseMap(d)
    }, [checkedList])

    useEffect(() => {
        if (newDaoId) {
            sessionStorage.removeItem("step");
            sessionStorage.removeItem("secondStep");
            sessionStorage.removeItem("firstStep");
            sessionStorage.removeItem("ImageUrl");
            // why use timer?
            dispatch({ type: 'LOADINGTYPE', payload: null });
            props.history.push(`/about/${newDaoId}`);
            dispatch({ type: 'RELOAD_DAOS', payload: true });
        }
    }, [newDaoId]);

    useEffect(() => {
        const firstStep = JSON.parse(sessionStorage.getItem('firstStep'));
        const imgUrl = JSON.parse(sessionStorage.getItem('imgUrl'));
        if (firstStep != null) {
            setdaoname(firstStep.name);
            setdesc(firstStep.description);
            setlogo(imgUrl);
        }
        console.log('firstStep:', firstStep)

        const secondStep = JSON.parse(sessionStorage.getItem('second'))
        if (secondStep) {
            setCheckedList(secondStep)
        }

    }, []);


    const Submit = async () => {
        const signer = web3Instance.getSigner(Account)
        try {
            dispatch({ type: 'LOADINGTYPE', payload: t('InstanceByTemplate') });
            const res = await maincontract.connect(signer).instanceByTemplate(
                [daoname, logo, desc], 
                "DaoTemplate", 
                checkedList
            );
            console.log('**** instanceByTemplate ', res)
            const r = await res.wait();
            const event = r.events.filter(e=>e.event === 'InstanceDAO')[0]
            console.log('filter InstanceDAO event: ', event)

            const newDaoId = event.args[0].toString()
            setNewDaoId(newDaoId)
        } catch (error) {
            console.error(error)
        }
    }
    
    return (
        <Content className="content">
            <Info>
                <div className="title">Template Selection</div>
                {/*<div className='detail'>{t('chooseTemplate')}</div>*/}
            </Info>

            <div className="listBrdr">
                {
                    list.map(i => (
                        <dl key={i.id} id={i.id} className={selected === i.id ? 'active' : ''}>
                            <dt >{i.name}</dt>
                            <div className="ddBox">
                                {i.components.map(item => (
                                    <dd key={item.name}><img src={item.img} alt=""/> {item.name}</dd>
                                ))}
                            </div>
                            {selected !== i.id && <Button className="useBtn" id={i.id} onClick={handleSelect}>
                                Use
                            </Button>}
                        </dl>
                    ))
                }
            </div>

            {/* <div className="listBrdr">
                <Checkbox.Group style={{width: '100%'}} onChange={onChange} value={checkedList}>
                    {
                        components.map(c => (
                            <Checkbox key={c.value} value={c.value} disabled><img src={c.img} alt=""/>{c.name}</Checkbox>
                        ))
                    }
                </Checkbox.Group>
            </div> */}

            <div className="line" style={{ textAlign: 'right', marginTop: '4rem' }}>
                <Button className="previous" style={{ marginRight: '3rem' }} onClick={toFirstStep}>
                    <img className="left" src={left} alt="" />
                    {t('think')}
                </Button>
                <Button type="primary" onClick={toThirdStep}>
                    {t('Next')}
                    <img src={right} alt="" />
                </Button>
                {/* <Button type="primary" onClick={()=>Submit()} className="nextBtn">
                    Submit
                    <img src={right} alt="" />
                </Button> */}
            </div>
        </Content>
    )
}
