const ManagerShowList = [
    "AuthManager", "VaultManager", "VoteManager", "OrgManager", "VenturesManager"
]

export default function About(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { orgcontract, maincontract, whoAmI, web3Instance, daoContractAddress, vaultcontract} = state;

    const [id, setAId] = useState(null);
    const [moderators, setModerators] = useState([]);
    const [moderatorShow, setmoderatorShow] = useState(true);
    const [activelist, setActivelist] = useState([]);
    const [balancelist, setbalancelist] = useState([]);
    const [balanceshow, setbalanceshow] = useState(true);
    const [contractshow, setcontractshow] = useState(true);
    const [owner, setowner] = useState('');

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [contractArr, setcontractArr] = useState([]);
    const [tokenlist, settokenlist] = useState([]);

    const [errorShow, seterrorShow] = useState(false);
    // const [nativeToken, setnativeToken] = useState(0);
    const [errorTips, seterrorTips] = useState('');

    const [moreDaos, setMoreDaos] = useState(false);


    useEffect(() => {
        setcontractArr([]);
        setcontractshow(true);
        setbalancelist([]);
        setbalanceshow(true);

        setAId(props.match.params.id);

    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);
   
    const getDaoContract = () => {
        if (!daoContractAddress) return
        let lst = []
        ManagerShowList.forEach(m=>{
            if (daoContractAddress[m]) {
                lst.push({
                    name: m,
                    address: daoContractAddress[m]
                })
            }
        })
        setcontractArr(lst)
        setcontractshow(false);
    }
   
    const updateToken = async (tokenlist, fund) => {
        setbalanceshow(false)
        setbalancelist(tokenlist)
    };

    useEffect(async()=>{
        if (!id || !mainConnect) return
        // await getDaoData();
    }, [id, mainConnect])

    useEffect(() => {
        getDaoContract()
    }, [daoContractAddress]);


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(async () => {
        if (!orgcontract) return
        try {
            const {lst, len} = await api.org.getDaoModeratorList(orgcontract, 0, 1000)
            setModerators(lst);
            setmoderatorShow(false);
        } catch (error) {
            console.error('getDaoModeratorList failed', error)
        }
    }, [orgcontract])


    const switchKey = (key) => {
        let str = '';
        let img = '';
        switch (key) {
            case 'base_addr':
                str = 'Base';
                img = BASE;
                break;
            case 'GovTokenManager':
                str = 'ERC20';
                img = ERC20;
                break;
            case 'OrgManager':
                str = 'ORG';
                img = ORG;
                break;
            case 'VaultManager':
                str = 'Vault';
                img = VAULT;
                break;
            case 'VoteManager':
                str = 'Vote';
                img = VOTE;
                break;
            case 'AuthManager':
                str = 'Auth';
                img = AUTH;
                break;
            case 'VenturesManager':
                str = 'Venture';
                img = VENTURE;
                break;
            default:
                str = key;
                img = '';
                break;
        }
        return {
            addr: str.toUpperCase(),
            img
        };
    }

    const setDaoOwner = (o) => {
        o && setowner(o)
    }


    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip>{errorTips}</Tip>
            </Modal>

            <div className='container'>
                <Left history={props.history} id={props.match.params.id} setDaoOwner={setDaoOwner} updateToken={updateToken} />
                <section>
                    <div className="titleTop">{t('Balance')}</div>
                    <Ul>
                        {
                            balanceshow && <LoadingNew />
                        }
                        {
                            !balanceshow && balancelist.map((item, index) =>
                                <li key={`balance_${index}`}>
                                    <BalanceNum>{item.balance}</BalanceNum>
                                    <Symbol>{item.symbol}</Symbol>
                                    {
                                        <Address>
                                            {item.address}
                                            <CopyStr address={item.address} />
                                        </Address>
                                    }

                                </li>
                            )
                        }
                        {/* {
                            !balanceshow && (
                                <li className='occ' onClick={()=>{addNewToken()}}>+</li>
                            )
                        } */}
                    </Ul>
                </section>
                <section>
                    <div className="titleTop">Moderators</div>
                    <UlMdrt>

                        {
                            moderatorShow && <LoadingNew />
                        }
                        {
                            !moderatorShow && owner && (
                                <li>
                                    <img src={OwnerImg} alt="" />
                                    <div>
                                        <Names>Owner</Names>
                                        <Address>{owner}</Address>
                                    </div>
                                </li>
                            )
                        }
                        {
                            !moderatorShow && moderators.map((item) => <li key={item.address}>
                                <img src={AdminImg} alt="" />
                                <div>
                                    <Names>{item.name}</Names>
                                    <Address>{item.address}</Address>
                                </div>
                            </li>)
                        }
                    </UlMdrt>
                </section>
                <section>
                    <div className="titleTop">Contracts</div>
                    {
                        contractshow && <LoadingNew />
                    }
                    {
                        !contractshow && <UlContr>{
                            contractArr.map((item) => (
                                <li key={`contract_${item.address}`}>
                                    <Imglft src={switchKey(item.name).img} alt="" />
                                    <ContractName>{switchKey(item.name).addr} </ContractName>
                                        <RhtBrdr>
                                            <Address>{item.address}</Address>
                                            <CopyStr address={item.address} alt="" />
                                        </RhtBrdr>

                                </li>
                            ))
                        }
                        </UlContr>
                    }
                </section>
                {
                    (!isModerator && !isOwner && !isMember) && <div>
                        <Other>Other DAOs</Other>
                        <MoreDaos history={props.history} title="Explorer other DAOs" sizeStatus="size"/>
                    </div>
                }

            </div>
        </div>
    )
}
