import React, {useState, useRef} from "react";
import {Button, Input, Modal,Radio, DatePicker} from "antd";
import styled from "styled-components";
import moment from "moment";
import { useWeb3 } from "../../api/contracts";
import Estimate from "./estimate";
import api from "../../api";
import { ethers } from "ethers"

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     font-family: "Roboto-Light"!important;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`


const LastTr = styled('div')`
 
  margin-top: 4.4rem;
  font-family: "Roboto-Light";
  color: #010643;
  .ant-radio-group{
    width: 100%;
     display: flex;
    justify-content: space-between;
  }
`


export default function RaiseLps(props) {
    const { show ,closeRaise, fund } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg } = state;

    const [value, setValue] = useState(0);

    const [maxDep, setMaxDep] = useState(null)
    const [minDep, setMinDep] = useState(null)

    const [raiseTime, setRaiseTime] = useState(null)

    const estimateRef = useRef()

    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }

    const handleRaiseTime = (v)=>{
        const dateTime = Date.parse(v._d)
        setRaiseTime(dateTime)
    }

    const handleInputChange = (e) =>{
        const {name, value} = e.target
        switch (name) {
            case 'maxDep':
                setMaxDep(Number(value))
                break
            case 'minDep':
                setMinDep(Number(value))
                break
            default:
                return
        }
    }

  
    const onChange = e => {
        console.log('radio checked', e.target.value);
        setValue(e.target.value);
    };

    const handleClose = () =>{
        closeRaise()
    }
    const confirmRaise = async ()=>{
        try {
            dispatch({ type: 'LOADINGTYPE', payload: 'Config LP Raise' });

            const erc20 = await api.erc20.getContract(web3Instance, fund.address);
            const deci =  await erc20.decimals()

            const signer = web3Instance.getSigner(Account)
            await api.venture.configLPraise(daoOrg, signer, {
                height: estimateRef.current.getEstimateHeight(), 
                minDep: ethers.utils.parseUnits(String(minDep), deci), 
                maxDep: ethers.utils.parseUnits(String(maxDep), deci), 
            })
            // TODO reload page
            handleClose()
        } catch (error) {
            console.error(error)
            console.error(error)
            const msg = (error.data && error.data.data && error.data.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `'Config failed: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >Raise for LPs</span>
        </div>

        <div className="label">Raise time</div>
        <div className="inputBrdr">
            <DatePicker
                className="myDatePicker"
                placeholder='Please choose raise time'
                style={{ width: '100%' }}
                format="YYYY-MM-DD HH:mm:ss"
                allowClear={false}
                disabledDate={disabledDate}
                onChange={handleRaiseTime}
                showTime={true}
                showNow={false} />
        </div>
        <Estimate time={raiseTime} ref={estimateRef} />

        <div className="label">Max deposit per LP</div>
        <div className="inputBrdr">
            <Input
                placeholder='Please enter'
                name='maxDep'
                value={maxDep}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{fund.symbol}</span>
        </div>
        <div className="label">Min deposit per LP</div>
        <div className="inputBrdr">
            <Input
                placeholder='Please enter'
                name='minDep'
                value={minDep}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{fund.symbol}</span>
        </div>
        <LastTr>
            <Radio.Group onChange={(e)=>onChange(e)} value={value}>
                <div><Radio value={0}>Do not use white list</Radio></div>
                <div><Radio value={1}>Add LP white list</Radio></div>
            </Radio.Group>
        </LastTr>

        <NextBrdr >
            <Button type="primary" onClick={confirmRaise} style={{ width: '100%', marginTop: '3rem' }}>
                Submit
            </Button>
        </NextBrdr>
    </ModalBox>
}
