import React, {useEffect, useState} from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";
import { useWeb3 } from "../../api/contracts";
import Arrow from '../../img/tianchongxing.png';
import Nodate from '../../img/nodate-whitelist.png';
import api from "../../api";
import { ethers } from "ethers"

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     font-family: "Roboto-Light"!important;
    }
    .labelTips{
      font-size: 1.4rem;
    }
    .star{
      color: #ff6f5a;
      margin-left: 1.5rem;
    }
    .pr{
      position: relative;
      img{
          width: 4.4rem;
          position: absolute;
          top:-1.5rem;
          left: 50%;
          margin-left: -2.2rem;
      }
    }
    .suffix {
        color: #A6A6B7 !important;
        font-size: 1.4rem !important;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`

const Nocontent = styled('div')`
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 6rem 0 ;
  div{
  text-align: center;
  }
  .nocontent{
    color: #878AA5;
    margin-top: 2.8rem;
    font-family: "Roboto-Light"!important;
  }
`


export default function Deposit(props) {
    const { show ,closeDeposit, showContent, fund, mintToken } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg, daoContractAddress, venturecontract, whoAmI } = state;

    const [fundAmount, setFundAmount] = useState()
    const [tokenAmount, setTokenAmount] = useState()
    const [maxAmount, setMaxAmount] = useState()

    const handleInputChange = (e) =>{
        const {name, value} = e.target
        if (name === "fundAmount") {
            // [NOTE] 1:1
            setFundAmount(Number(value))
            setTokenAmount(Number(value))
        } 
    }


    const handleClose = () =>{
        closeDeposit()
    }

    const confirm = async () => {
        try {
            // approve
            dispatch({ type: 'LOADINGTYPE', payload: "Approving" });
            const signer = web3Instance.getSigner(Account)
            const erc20 = await api.erc20.getContract(web3Instance, fund.address);

            let allowanceAmount = await erc20.connect(signer).allowance(Account, daoContractAddress.VaultManager);
            if (!parseInt(allowanceAmount.toString())) {
                const res = await erc20.connect(signer).approve(daoContractAddress.VaultManager, ethers.utils.parseEther(String(Math.max(Number(fundAmount)*2, 100))), { gasLimit: 50000 });
                await res.wait()
            }

            dispatch({ type: 'LOADINGTYPE', payload: "depositing" });
            await api.venture.raise(daoOrg, signer, ethers.utils.parseUnits(String(fundAmount), await erc20.decimals()))

        } catch (error) {
            console.error('deposit failed', error)
            const msg = (error.data && error.data.data && error.data.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `deposit failed: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
            handleClose()
        }
        
    }

    useEffect(()=>{
        if (!venturecontract || !whoAmI || !fund || !fund.address) return
        const getMax = async()=>{
            // TODO check gp or lp
            try {
                let max
                console.log(whoAmI[2], whoAmI[0])
                // max = await venturecontract.GpMaxSubAmount()
                if (whoAmI[2] || whoAmI[0]) {
                    max = await venturecontract.GpMaxSubAmount()
                    console.log("GpMaxSubAmount: ", max.toString())
                } else {
                    max = await venturecontract.LpMaxSubAmount()
                    console.log("LpMaxSubAmount: ", max.toString())
                }
                const erc20 = await api.erc20.getContract(web3Instance, fund.address);
                
                setMaxAmount(Number(ethers.utils.formatUnits(max, await erc20.decimals())))
            } catch (error) {
                console.error('gpMaxSubAmount failed', error)
            }
        }
        getMax()
    }, [venturecontract, whoAmI, fund])

   
    return <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >Deposit Modal</span>
        </div>
        {
            showContent &&<div>
                <div className="label">Deposit <span className="star">*</span><span className="labelTips">Your funds will be 1:1mint to token</span></div>
                <div className="inputBrdr">
                    <Input
                        placeholder='Please enter'
                        name='fundAmount'
                        value={fundAmount}
                        autoComplete="off"
                        type='number'
                        onChange={(e)=>handleInputChange(e)}
                    />
                    <span className='suffix'>{fund.symbol}</span>

                </div>
                <div className="label pr">Mint token <img src={Arrow} alt=""/></div>
                <div className="inputBrdr">
                    <Input
                        placeholder='Please enter'
                        name='tokenAmount'
                        value={tokenAmount}
                        autoComplete="off"
                        type='number'
                        disabled
                        onChange={(e)=>handleInputChange(e)}
                    />
                    <span className='suffix'>Max: {maxAmount} {mintToken.symbol}</span>
                </div>

                <NextBrdr >
                    <Button disabled={!fundAmount || fundAmount < 0 || fundAmount > maxAmount} type="primary" onClick={confirm} style={{ width: '100%', marginTop: '3rem' }}>
                        Submit
                    </Button>
                </NextBrdr>
            </div>
        }
        {
            !showContent && <Nocontent>
                <div>
                    <div>
                        <img src={Nodate} alt=""/>
                    </div>
                    <div className="nocontent">Sorry, it's not on the white list</div>
                </div>


            </Nocontent>
        }

    </ModalBox>
}
