import axios from 'axios';

const ServerEndpoint = 'https://test-backend.subdao.org'

const POST = (data)=>{
    return axios({
        method: 'POST',
        url: `${ServerEndpoint}/api/query`,
        headers: {
            'Content-Type': 'application/json'
        },
        data: data
    })
}

export const getFavorite =async () => {
 let data = await axios({
        method: "GET",
        url: `https://raw.githubusercontent.com/SubDAO-Network/daos/main/daos.json`,
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
return data;
};



export const getInvestProjects = async (contractAddress, signer, page, size) => {
    const data = {
        query: `{
            projects(cond: {contractAddress: "${contractAddress}", sign:"${signer}"}, page:{ page: ${page}, size: ${size}, sortField:"create_at", order:"desc"}) {
                page
                size
                total
                data {
                    txId
                    creator
                    name
                    intro
                    logo
                    jump_addr
                    tx
                    create_at
                }
            }
        }`
    }
    return POST(data)
}

export const uploadProject = async (data) => {
    return POST({
        query: `mutation{
            createProject(sign: {sign: "${data.signer}", dao_address: "${data.daoAddress}"}, project: {
              txId: "${data.txId}", creator: "${data.creator}", name: "${data.name}", intro: "${data.intro}", logo: "${data.logo}", jump_addr: "${data.jump_addr}", tx: "${data.tx}"
            }) {
                create_at
                creator
                intro
                jump_addr
                logo
                name
                tx
                txId
            }
        }`
    })
}


export const uploadImg = async (fileData) => {
    const formData = new FormData();

    const query = 'mutation ($file: Upload!) { fileUpload(file: $file) }'
    const variables = { file: null }

    formData.append("operations", JSON.stringify({ query, variables}));
    formData.append("map", JSON.stringify({"0": ["variables.file"]}));
    formData.append("0", fileData);

    return POST(formData)
}
