    let { t } = useTranslation();

    const handleClicktoVote = () => {
        props.history.push(`/vote/${props.match.params.id}`)
    }
    const { native_token } = window.mainAddress;
    useEffect(() => {
        if (!daoContractAddress) return
        const setOneVote = async () => {
            const vote_id = parseInt(props.match.params.voteid)
            try {
                const vdata = await api.vote.getVote(votecontract, vote_id)
                setendtime(vdata.endDisplay)
                setvoteid(vote_id);
                if (vdata.isExecuted) vdata.status = 1
                try {
                    if (!vdata.isExecuted && !vdata.isOpen) {
                        const canExecute =  (await votecontract.canExecute(vote_id)).toString()
                        if (canExecute === '0') {
                            vdata.status = 2    // not trigger
                        } else {
                            vdata.status = 0
                        }
                    }
                } catch (error) {
                    console.error("canExecute failed", error)
                }
                
                setoptionlist([
                    {
                        name: 'agree',
                        value: vdata.yea
                    },
                    {
                        name: 'disagree',
                        value: vdata.nay
                    }
                ])

                setVoteData(vdata)

                setsupport(vdata.minRequire);
                setsupportPercent(vdata.supportRequiredPct);


            } catch (error) {
            }
            // await api.vote.queryOneVote(votecontract, props.match.params.voteid).then(data => {
            //     if (!data) return;
            //     const {
            //         vote_id, title, desc, support_require_num, min_require_num, choices, to_address, transfer_value, start_date, vote_time, executed, need_trigger,erc20_symbol,status,erc20_address
            //     } = data;

            //     setendtime(PublicJS.formatvoteDateTime(start_date, vote_time));

            //     setneed(need_trigger);
            //     setStatusS(status)
            //     seterc20_symbol(erc20_symbol);
            //     settitle(title);
            //     setexecuted(executed);
            //     setvoteid(vote_id);
            //     setdesc(desc);
            //     settoaddress(to_address);

            //     if(native_token === erc20_address){
            //         let myNum = transfer_value.replace(/,/g,'');
            //         let myValue = parseInt(myNum) / Math.pow(10, decimals);
            //         settovalule(myValue);
            //     }else{
            //         settovalule(transfer_value);
            //     }

            //     setsupport(support_require_num);
            //     setmin(min_require_num);
            //     let arr = [], afterArr = [];
            //     arr = choices.split('|');
            //     arr.map((i, index) => {
            //         let obj = i.split(":");
            //         afterArr[index] = {
            //             value: obj[1] ? parseInt(obj[1]) : 0,
            //             name: obj[0]
            //         };
            //         return i;
            //     });
            //     if (afterArr.length) {
            //         setoptionlist(afterArr);
            //     }

            // });
        };

        setOneVote();
    }, [daoContractAddress]);

    useEffect(()=>{
        const getVoteMore = async()=>{
            if (!voteData) return
            if (voteData.isTransfer && !vaultcontract) return
            if (!voteData.isTransfer && !daoContractAddress) return
            try {
                if (voteData.isTransfer) {
                    const wdata = await api.vault.getTransferDetail(vaultcontract, voteData.wid)
                    settoaddress(wdata.proposer)
                    const erc20 = await api.erc20.getContract(web3Instance, wdata.token)
                    seterc20_symbol(await erc20.symbol())
                    settovalule(ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals()))
                } else if (voteData.isApprove) {
                    const contract = await api.vote.getGrantContract(web3Instance, daoContractAddress["GrantMethodManager"])
                    const data = await contract.registerVotes(voteData.wid)
                    console.log("data:",data)
                    // TODO show
                } else if (voteData.isKick || voteData.isPeriod) {
                    // const contract = await api.vote.getExecuteContract(web3Instance, daoContractAddress["VoteExecutionManager"])
                    // TODO show
                }
                
            } catch (error) {
                console.error("getMoreVoteInfo failed", error)
            }
        }
        getVoteMore()
    }, [voteData, vaultcontract, daoContractAddress])

