import React, { useEffect, useRef, useState } from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";
import { useWeb3 } from "../../api/contracts";
import Logo from "../upload/img"
import { uploadProject } from "../../api/apiHTTP"


const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     font-family: "Roboto-Light"!important;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`

const TextAreaDiv = styled.div`
    .textarea {
        height: 19.1rem !important;
    }
`


export default function AddProject(props) {
    const { show , closeModel } = props;

    const { state } = useWeb3();
    const { Account } = state;

    const { TextArea } = Input

    const [name, setName] = useState(null)
    const [homepage, setHomepage] = useState(null)
    const [amount, setAmount] = useState(null)
    const [desc, setDesc] = useState(null)

    const imgRef = useRef()


    const handleInputChange = (e) =>{
        const {name, value} = e.target
        switch (name) {
            case 'name':
                setName(value)
                break
            case 'homepage':
                setHomepage(value)
                break
            case 'amount':
                setAmount(value)
                break
            case 'desc':
                setDesc(value)
                break
            default:
                return
        }
    }

    const handleClose = () => {
        imgRef.current.clear()
        closeModel()
    }

    const requestSign = async(exampleMessage) => {
        try {
            const from = Account;
            const msg = `0x${Buffer.from(exampleMessage, 'utf8').toString('hex')}`;
            const sign = await window.ethereum.request({
                method: 'personal_sign',
                params: [msg, from],
            });
            console.log(sign);
            return sign
        } catch (err) {
            console.error(err);
        }
    }

    const confirmRaise = async ()=>{
        // upload img
        let imgCode
        imgCode = await imgRef.current.upload()
        if (!imgCode) return

        // sign
        let signer
        try {
            console.log("imgCode: ", imgCode)
            signer = await requestSign(imgCode)
        } catch (error) {
            console.error("sign failed: ", error)
            return
        }
        // upload project
        try {
            // TODO hardcode
            uploadProject({
                signer, daoAddress: "qqqq",
                txId: "mnmn", creator: Account, name: "good", intro: "nice", logo: imgCode, jump_addr: "asdf", tx: "bhuj"
            })
        } catch (error) {
            console.error("Upload Project failed: ", error)
        }
        
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span>Upload project</span>
        </div>
        <div className="label">Project logo</div>
        <Logo ref={imgRef} />

        <div className="label">Project name</div>
        <div className="inputBrdr">
            <Input
                placeholder='Please enter'
                name='name'
                value={name}
                autoComplete="off"
                onChange={(e)=>handleInputChange(e)}
            />
        </div>
            
        <div className="label">Project homepage</div>
        <div className="inputBrdr">
            <Input
                placeholder='Please enter'
                name='homepage'
                value={homepage}
                autoComplete="off"
                onChange={(e)=>handleInputChange(e)}
            />
        </div>

        <div className="label">Invest amounts</div>
        <div className="inputBrdr">
            <Input
                placeholder='Please enter'
                name='amount'
                value={amount}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{props.symbol}</span>
        </div>
        <div className="label">Project description</div>
        <TextAreaDiv className="inputBrdr">
            <TextArea
                className="textarea"
                placeholder='Please enter description'
                name='desc'
                value={desc}
                onChange={handleInputChange}
            />
        </TextAreaDiv>

        <NextBrdr >
            <Button type="primary"  style={{ width: '100%', marginTop: '3rem' }} onClick={confirmRaise}>
                Submit
            </Button>
        </NextBrdr>
    </ModalBox>
}
