import { Button, Modal } from 'antd';
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import api from '../../api';
import { useWeb3 } from "../../api/contracts";

const ModalBox = styled(Modal)`
     .defaultBtn{
        font-size: 1.8rem !important;
         font-family: "Roboto-Regular";
      }
      .confirmInnerTitle{
      margin-bottom: 4rem;
      }
`

export default function ConfirmBox(props) {

    let { t } = useTranslation();
    let { handleClose, show, tips } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg } = state;

    const tipsList = [
        "",
        "Close Venture",
        "Refund",
    ]

    const closeVenture = async()=>{
        try {
            dispatch({ type: 'LOADINGTYPE', payload: 'Closing Venture' });
            const signer = web3Instance.getSigner(Account)
            await api.venture.closeVenture(daoOrg, signer)
            handleClose()
        } catch (error) {
            console.error('close failed', error)
            const msg = (error.data && error.data.data && error.data.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `close failed: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    const refund = async()=>{
        // TODO
    }

    const handleConfirm = async()=>{
        if (tips === 1) {
            closeVenture()
        } else if (tips === 2) {
            refund()
        }
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null} centered={true} maskClosable={false}>
        <div className="confirmInnerTitle">{tipsList[tips]}</div>
        <div className="confirmInnerBtnGroup">
            <Button className="defaultBtn" onClick={()=>handleClose()} >
             Cancel
            </Button>
            <Button className="defaultBtn" type="primary" onClick={()=>handleConfirm()}>
                Confirm
            </Button>

        </div>

    </ModalBox>;
}


