export const DaoType = {
    Normal: 0,
    Venture: 1
}

export default function Left(props) {
    const { state, dispatch } = useWeb3();
    const { web3Instance, daoOrg, stockcontract, vaultcontract, orgcontract, daoContractAddress, Account, apiState, erc20contract, votecontract, maincontract, allAccounts, reloadbalance } = state;
    const [delMem, setdelMem] = useState(false);
    const [delAdmin, setdelAdmin] = useState(false);
    const [info, setinfo] = useState(true);

    const [owner, setOwner] = useState('');
    const [ownerBalance, setOwnerBalance] = useState(0);
    const [symbol, setSymbol] = useState('');
    const [tokens, setTokens] = useState([]);

    const [size, setSize] = useState('');
    const [description, setDescription] = useState('');
    const [logo, setLogo] = useState('');
    const [name, setName] = useState('');
    const [type, setType] = useState('');
    const [showModal, setShowModal] = useState(false);

    const [daoBase, setDaoBase] = useState({})

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);
    const [more, setmore] = useState(false);
    const [newWithdraw, setnewWithdraw] = useState(false);

    const [addshow, setaddshow] = useState(false);
    const [applyAuth, setapplyAuth] = useState(false);
    const [addapplyshow, setaddapplyshow] = useState(false);
    const [propsId, setPropsId] = useState(null);

    const [contractlist, setcontractlist] = useState({
        base_addr: null,
        erc20_addr: null,
        OrgManager: null,
        VaultManager: null,
        VoteManager: null,
        AuthManager: null,
    });

    const updateToken = props.updateToken

    let setDaoOwner = props.setDaoOwner
    if (!setDaoOwner) setDaoOwner = ()=>{}

    useEffect(()=>{
        const setBase = () => {
            if (Object.keys(daoBase).length === 0) return;
            let { owner, name, logo, desc } = daoBase;
            setName(name);
            setLogo(logo);
            setDescription(desc);
            setOwner(owner);
            setDaoOwner(owner)
            // setSize(size);
            setinfo(false);
        };
        setBase();
    }, [daoBase])

    const init = async () => {
        let pageType = props.history.location.pathname.split('/')[1];
        setType(pageType);

        let data
        try {
            const id = ethers.BigNumber.from(propsId)
            data = await maincontract.instanceMap(id);
        } catch (error) {
        }
        if (!data) return

        setDaoBase(data)
        console.log('dao:', data)

        let components = []
        let templateConfig

        try {
            const daoOrg = await api.daoOrg.getContract(web3Instance, data.daoOrganization) 
            dispatch({type: 'SET_DAOORG', payload: daoOrg})

            const templateConfigAddress = await daoOrg.templateConfig()
            templateConfig = await api.template.getContract(web3Instance, templateConfigAddress)
            components = await templateConfig.listComponents(0, 10);
        } catch (error) {
            console.error('listComponents failed', error)
        }
        

        const clabels = []
        const reqs = components.map(address=>{
            return new Promise((resolve, reject)=>{
                templateConfig.componentAddrToLabel(address).then(data=>{
                    resolve(data)
                }).catch(err=>{
                    reject(err)
                })
            })
        })
        try {
            const result = await Promise.all(reqs)
            const cdict = {}
            for (let i=0; i<components.length; i++) {
                clabels.push({
                    name: result[i],
                    address: components[i]
                })
                cdict[result[i]] = components[i]
                console.log(`* ${result[i]}: ${components[i]}`)
            }
            setcontractlist(cdict)
            dispatch({type: 'SET_DAO_CONTRACT', payload: cdict})
            if (cdict.VenturesManager && props.setDaoType) {
                props.setDaoType(DaoType.Venture)
            }
        } catch (error) {
            console.error('componentAddrToLabel failed', error)
        }
    }

    const [showTransfer, setShowTransfer] = useState(false);

    const withdrawRef = useRef();
    useEffect(() => {
        if (orgcontract == null || !delMem) return;
        const setAdmin = async () => {
            dispatch({ type: 'LOADINGTYPE', payload: 'Quitting' });
            if (isModerator) {
                await api.org.resignModerator(orgcontract, function (result) {
                    if (!result) return;
                    setdelAdmin(true)
                }).catch((error) => {

                    dispatch({ type: 'LOADINGTYPE', payload: null });
                    dispatch({ type: 'MSGTYPE', payload: { msg: `Resign Moderator: ${error.message}` } });
                    // seterrorShow(true);
                    // seterrorTips(`Resign Moderator: ${error.message}`);
                    // setLoading(false);

                });
            } else {
                setdelAdmin(true)
            }
        };
        setAdmin();

    }, [delMem]);


    // const setBalance = async () => {
    //     let bal = await api.erc20.BalanceOf(erc20contract);
    //     setOwnerBalance(bal);
    //     let result = await api.erc20.queryInfo(erc20contract);
    //     if (!result) return;
    //     setSymbol(result.symbol)
    // };

    useEffect(() => {
        if (!reloadbalance) return;
        getTokens()
        dispatch({ type: 'BALANCE', payload: null });
    }, [reloadbalance]);
    const queryAddrs = async () => {
        // await api.dao.queryComponentAddrs(daoManagercontract).then(data => {
        //     if (data) {
        //         let arr = [];
        //         Object.keys(data).map((item) => {
        //             arr.push({
        //                 name: item,
        //                 address: data[item]
        //             });
        //             return item;
        //         });
        //         sessionStorage.setItem('contractArr', JSON.stringify(arr));
        //         setcontractlist(data);
        //     }
        // });
    };
    // useEffect(() => {
    //     if (daoManagercontract == null) return;
    //     queryAddrs();
    // }, [daoManagercontract, propsId, maincontract]);

    useEffect(async () => {
        const { OrgManager, VaultManager, VoteManager, VenturesManager, VenturesStockManager } = contractlist;
        if (!OrgManager || !VaultManager || !VoteManager) return
        const initComponentContract = async () => {
            try {

                const orgContract = await api.org.getContract(web3Instance, OrgManager)
                dispatch({type: 'SET_ORG', payload: orgContract})
            } catch (error) {
                console.error(`getContract OrgManager failed ${OrgManager}`, error)
            }
            try {
                const vaultcontract = await api.vault.getContract(web3Instance, VaultManager)
                dispatch({type: 'SET_VAULT', payload: vaultcontract})
            } catch (error) {
                console.error(`getContract VaultManager failed ${VaultManager}`, error)
            }
            try {
                const voteContract = await api.vote.getContract(web3Instance, VoteManager)
                dispatch({type: 'SET_VOTE', payload: voteContract})
            } catch (error) {
                console.error(`getContract VoteManager failed ${VoteManager}`, error)
            }
            if (VenturesManager) {
                try {
                    const ventureContract = await api.venture.getContract(web3Instance, VenturesManager)
                    dispatch({type: 'SET_VENTURE', payload: ventureContract})
                    const stockContract = await api.venture.getStockContract(web3Instance, VenturesStockManager)
                    dispatch({type: 'SET_STOCK', payload: stockContract})
                } catch (error) {
                    console.error(`getContract ventureContract failed ${VenturesManager}`, error)
                }
            }
            
        }
        initComponentContract()

    }, [contractlist]);

    // useEffect(() => {
    //     if (orgcontract == null || !delAdmin || !delMem) return;
    //     setTimeout(async () => {
    //         window.location.reload()
    //     }, 5000)
    // }, [delAdmin]);

    useEffect(() => {
        if (maincontract == null) {
            dispatch({ type: 'LOAD_MAINCONTRACT' });
            return;
        }
        init();
    }, [propsId, maincontract]);

    useEffect(() => {
        setPropsId(props.id)
    }, [props.id]);

    const whoAmI = async () => {
        try {
            await api.org.whoAmI(orgcontract, Account).then(data => {
                if (!data) return;
                setisOwner(data[0]);
                setisModerator(data[1]);
                setisMember(data[2]);
                dispatch({ type: 'WhO_AM_I', payload: data });
            });
        } catch (error) {
            console.error("whoAmI failed", error)
        }
        
    };
    useEffect(() => {
        if (!orgcontract || !Account) return;
        whoAmI();
    }, [orgcontract, propsId, maincontract]);

    const getToken = async (address)=>{
        try {
            const mockERC20 = await api.erc20.getContract(web3Instance, address)

            const symbol = await mockERC20.symbol()
            const deci = await mockERC20.decimals() 
            // user blance
            const userBalance = ethers.utils.formatUnits(await mockERC20.balanceOf(Account), deci)
            // vault balance
            let balance = await api.vault.getTokenBalance(vaultcontract, address)
            balance = ethers.utils.formatUnits(balance, deci)
            console.log(symbol, deci, balance, address, userBalance)
            return { symbol, deci, balance, address, userBalance }

        } catch (error) {
            console.error(error)
            return {}
        }
    }
    const getTokens = async()=>{
        let data = await api.vault.getTokenList(vaultcontract)

        if (!data || data.length === 0) {
            return
        }

        let reqs = []
        data.forEach(tokenAddress=>{
            reqs.push(getToken(tokenAddress))
        })
        const result = await Promise.all(reqs)

        let lst = []
        for (let i=0; i<result.length; i++) {
            let tdata = result[i]
            lst.push({
                symbol: tdata.symbol,
                address: tdata.address,
                balance: tdata.balance
            })
            if (tdata.userBalance) setOwnerBalance(tdata.userBalance)
        }

        updateToken && updateToken(lst)

        console.log('*** [getTokenList] ***', lst)

        lst.length > 0 && setSymbol(lst[0].symbol)
        setTokens(lst)
    }

    useEffect(()=>{
        if (!vaultcontract) return
        getTokens()
    }, [vaultcontract, reloadbalance])


    useEffect(()=>{
        if (!daoContractAddress || !stockcontract) return
        const getStockToken = async()=>{
            const deci = await stockcontract.decimals()
            let mintToken = {
                address: daoContractAddress['VenturesStockManager'],
                symbol: await stockcontract.symbol(),
                balance: ethers.utils.formatUnits(await stockcontract.totalSupply(), deci),
                userBalance: ethers.utils.formatUnits(await stockcontract.balanceOf(Account), deci)
            }
            props.updateMintToken && props.updateMintToken(mintToken)
        }
        getStockToken()
    }, [daoContractAddress, stockcontract])

    const handleTransfer = () => {
        setShowTransfer(true)

    };

    const handleClose = () => {
        setShowTransfer(false);
    };

    const handleExitClose = () => {
        setShowModal(false)
    };
    const handleClicktoType = (typeNow) => {
        if (typeNow === type) return;

        props.history.push(`/${typeNow}/${propsId}`);
    }
    const handleExit = () => {
        setShowModal(true)
    }

    const joinDaos = async () => {
        setaddapplyshow(true)
        // await api.org.getFreeAddMember(orgcontract).then(data => {
        //     data ? setaddshow(true) : setaddapplyshow(true)
        // });
    }

    const handleExitConfirm = async () => {
        dispatch({ type: 'LOADINGTYPE', payload: 'Quitting' });
        setShowModal(false);
        try {
            const signer = web3Instance.getSigner(Account)
            await api.org.leaveDao(daoOrg, signer)
        } catch (error) {
            console.error(error)
            const msg = (error && error.data && error.data.message) || 'quit failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `Resign Member: ${msg}` } });
            
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    //add members begin
    const handleAddClose = () => {
        setaddshow(false)
    }


    const setall = () => {
        // setModeratorFunc()
        // setMemberFunc()
        window.location.reload()

    }
    const handleApplyClose = () => {
        setaddapplyshow(false)
    }
    // const handleaddApply = () => {
    //     setaddapplyshow(true)
    // }
    const handleAppTips = () => {
        // setshowTips(true)
    }
    const handleAppTipsClose = () => {
        // setshowTips(false)
    }


    const handleClicktoDetail = () => {
        setnewWithdraw(true)

    }

    const handleWithDrawClose = () => {
        setnewWithdraw(false)
    }
    const setShowWithDraw = async () => {


        let obj = withdrawRef.current.resultToVault();


        // setTipsNum(obj.amount)
        // setTipsAddress(obj.address)
        // setType(false)
        // withdrawRef.current.amountToNull()


        // beforelen = historylist.length
        //
        // if (afterlen > beforelen) {
        //     setshowvaultTips(true);
        //     setTimeout(async () => {
        //         setshowvaultTips(false)
        //     }, 3000)
        // }else{
        //     setshowError(true);
        //     setTimeout(async () => {
        //         setshowError(false)
        //     }, 3000)
        // }



    }

    return (
        <div>
            <Transfer
                daoId={propsId}
                showTips={showTransfer}
                handleClose={handleClose}
            />
            <ExitOrg
                handleClose={handleExitClose}
                handleConfirm={() => handleExitConfirm()}
                showTips={showModal} />

            <Addnew
                handleClose={handleAddClose}
                showTips={addshow}
                typeName='Members'
                refresh={setall}
                handleBatch={false}
                applyAuth={applyAuth}
            />
            <AddApply
                handleClose={handleApplyClose}
                showTips={addapplyshow}
                handleTips={handleAppTips}
                refresh={setall} />

            {
                !info && <TopTitles>
                    <LftTop>
                        <img className="logo" src={logo} alt="" />
                        <Contents>
                            <Tit>
                                <div className="title">{name}</div>
                                {
                                    Account && <div>
                                        {(isOwner || isMember || isModerator) && <div>
                                            {
                                                isOwner && <Button type="text" onClick={() => handleTransfer()}>
                                                    <img src={tranfer} alt="" />
                                                    Transfer Ownership
                                                </Button>
                                            }
                                            {(isMember || isModerator) && !isOwner &&
                                                <Button type="text" onClick={() => handleExit()}>
                                                    <img src={quit} alt="" />
                                                    Quit
                                                </Button>
                                            }
                                        </div>
                                        }
                                        {
                                            (!isOwner && !isMember && !isModerator) && <Button type="text" onClick={() => joinDaos()}><img src={Join} alt="" />Join</Button>
                                        }
                                    </div>
                                }

                            </Tit>
                            <div className="members">{size} Member(s)</div>
                            <div className="contentDesc">
                                {description}
                                {description.length > 110 && <div className="more" onClick={() => { setmore(true) }}>…More</div>}
                                <Modal
                                    className="moreModal"
                                    visible={more}
                                    onCancel={() => setmore(false)}
                                    footer={null}
                                    closable={false}
                                    getContainer={false}
                                    width={'91.4rem'}
                                    maskStyle={{ background: 'transparent' }}
                                    style={{ position: 'absolute', top: '30rem', left: '40rem' }}
                                >
                                    <div className="text">{description}</div>
                                </Modal>
                            </div>
                        </Contents>
                    </LftTop>
                    {
                        Account != null && <RhtTop>
                            <div className="title">My balance</div>
                            <div className="line">
                                <div className="num">{ownerBalance}</div>
                                <div className="text">{symbol}</div>
                                <Withdraw
                                    handleClose={handleWithDrawClose}
                                    showTips={newWithdraw}
                                    setShow={() => setShowWithDraw()}
                                    ref={withdrawRef}
                                    tokenlist={tokens}
                                    type="erc20"
                                />
                                <img src={accounts} alt="" onClick={() => handleClicktoDetail()} />
                            </div>
                        </RhtTop>
                    }
                    <a href="" className='twitter'>
                        <img src={twitter} alt="" />
                    </a>

                </TopTitles>
            }
            {
                info && <TopTitles><LoadingNew /></TopTitles>
            }

            {
                !props.removeGroup && <BtnGroup>
                    <span className={type === 'about' ? 'active' : ''} onClick={() => handleClicktoType('about')}>Home</span>
                    {
                        contractlist.VoteManager != null && <span className={type === 'vote' ? 'active' : ''} onClick={() => handleClicktoType('vote')}>Vote</span>
                    }
                    {
                        contractlist.VaultManager != null && <span className={type === 'vault' ? 'active' : ''} onClick={() => handleClicktoType('vault')}>Vault</span>
                    }
                    {
                        contractlist.OrgManager && <span className={type === 'org' ? 'active' : ''} onClick={() => handleClicktoType('org')}>ORG</span>
                    }
                    <span className={type === 'venture' ? 'active' : ''} onClick={() => handleClicktoType('venture')}>Venture</span>
                    <span className={type === 'venturePanel' ? 'active' : ''} onClick={() => handleClicktoType('venturePanel')}>Venture panel</span>
                </BtnGroup>
            }
        </div>
    )
}

