const PageBrdr = styled.div`
    width: 100%;
    text-align: center;
    margin-bottom: 40px;
    button{
      border-radius: 0!important;
    }
`;

const DaosModal = (props) => {
    const { moreDaos, handleClose } = props;

    const { state, dispatch } = useWeb3();
    const { maincontract, Account, daoManagercontract, daoType, reloadDaos,addresslist,mylist, web3Instance } = state;
    const [alls, setAlls] = useState(true);
    const [imglist, setimglist] = useState([]);
    const [showCreatDaoBtn, setshowCreatDaoBtn] = useState(true);
    const [pageTotal, setPageTotal] = useState(0);
    const [page, setPage] = useState(1);
    const { pathname } = useLocation()

    const pageSize = 10

    useEffect(() => {
        if (pathname === '/create') {
            setshowCreatDaoBtn(false)
        } else {
            setshowCreatDaoBtn(true)
        }
    }, [pathname])


    useEffect(() => {
        if (!maincontract || !page) return;
        setAlls(true);
        let mydaolist = [];
        const getMylist = async ()=>{
            if (!Account) return
            const res = await maincontract.listDaoInstanceByAccount(Account, page-1, pageSize);
            console.log('[listDaoInstanceByAccount]', res.daos)
            res.daos.forEach(d=>{
                if (d.name && d.logo && d.desc) {
                    mydaolist.push({
                        name: d.name,
                        logo: d.logo,
                        desc: d.desc,
                        address: d.daoOrganization,
                        key: d.id.toString(),
                        id: d.id,
                        owner: d.owner,  // account
                    })
                }
            })
            // console.log(`total: ${res.t.toString()} curpage: ${parseInt(res.p.toString())} size: ${res.s.toString()}`)
            setPage(parseInt(res.p.toString())+1);
            setPageTotal(parseInt(res.t.toString()))

            setimglist(mydaolist);
            setAlls(false);
        }
        const getAllList = ()=> {
            setimglist([])
            setPage(1)
            setPageTotal(0)
            setAlls(false);
        }
        if(daoType === 'my'){
            getMylist();
        } else if (daoType === 'all') {
            getAllList()
        }
    }, [page, maincontract, daoType]);

    const handleClick = () => {
        if (Account != null) {
            props.history.push('/create');
            dispatch({ type: 'DAOTYPE', payload: null });
        }
    }

    const handleClicktoAbout = (id, owner) => {
        props.history.push(`/about/${id}`);
        dispatch({ type: 'DAOTYPE', payload: null });
    }
    const handleSearch = (e) => {
        // let list = JSON.parse(sessionStorage.getItem('addresslist'));

        let arr;
        if (e.target.value.length) {
            arr = imglist.filter(item => {
                return item.name.toLowerCase().indexOf(e.target.value.toLowerCase()) > -1
            });
        } else {
            arr = imglist;
        }
        setimglist(arr);
    }
    const switchModal = () => {
        dispatch({ type: 'DAOTYPE', payload: 'all' });
    }
    const handlePage = (p) =>{
        setPage(p)
    }
    return (
        // <Animate animation="move-down">
        // <Animate animation="zoom-left"  showProp="show">
        <Modal
            visible={moreDaos}
            onCancel={handleClose}
            className='daoModal'
            footer={null}
            mask={false}
            transitionName="ant-move-down"
        >
            <DaoBody>
                <div className="top">

                    {
                        daoType === 'all' && <div className="left">
                            <img src={searchImg} alt="" />
                            <Inputbrdr type="text" placeholder="Search" onChange={(e) => handleSearch(e)} />
                        </div>
                    }
                    {
                        daoType === 'my' && <div className="left mydao">My DAOs</div>
                    }

                    {showCreatDaoBtn &&
                        <Button type="primary" onClick={() => handleClick()} style={{ width: '20rem', height: '6rem' }}>
                            Create DAO
                        </Button>}
                </div>
                <div className="daos">
                    {
                        alls && <LoadingNew />
                    }
                    {
                        !alls && !!imglist.length && imglist.map((item) =>
                            <div key={item.key} className="daoItem" onClick={() => handleClicktoAbout(item.key, item.owner)}>
                                <img src={item.logo} alt="" />
                                <div className="title">{item.name}</div>
                                <div className="detail">{item.desc}</div>
                            </div>
                        )
                    }

                    {
                        !alls && !imglist.length && <Bgmid>
                            <Imgbrdr src={nodaos} alt="" />
                        </Bgmid>
                    }
                    {
                        pageTotal>10 && <PageBrdr>
                            <Pagination defaultCurrent={page} total={pageTotal} pageSize={pageSize} onChange={handlePage}/>
                        </PageBrdr>
                    }

                    <div className="bottom">

                        {
                            !alls && !imglist.length && <span>Sorry,there is no DAOs</span>
                        }
                        {
                            daoType === 'my' && <Tips onClick={() => switchModal()}>
                                Explorer other DAOs ...
                            </Tips>
                        }
                    </div>
                </div>
            </DaoBody>
        </Modal>

    );
}

export default DaosModal;
