const buyToken = async (daoOrg, signer, obj)=>{
    const templateAction = ethers.utils.defaultAbiCoder.encode(
        ['address', 'bytes'],
        [
            window.mainAddress.actionVaultExeWithdrawToken,
            ethers.utils.defaultAbiCoder.encode(
                // address router, address WETH, address tokenIn, address tokenOut, uint256 amountIn, uint256 amountOutMin
                ['address', 'address', 'address', 'address', 'uint256', 'uint256'],
                [obj.router, obj.WETHAddress, obj.tokenIn, obj.tokenOut, obj.amountIn, obj.amountOut]
            )
        ]
    );
    const res =  await daoOrg.connect(signer).action(templateAction);
    await res.wait()
}

