import React, {useState} from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";

import Arrow from '../../img/tianchongxing.png';
import Cart from '../../img/gouwuche.png';

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      //max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     font-family: "Roboto-Light"!important;
    }
    .labelTips{
      font-size: 1.4rem;
    }
    .star{
      color: #ff6f5a;
      margin-left: 1.5rem;
    }
    .pr{
      position: relative;
      img{
          width: 4.4rem;
          position: absolute;
          top:-1.5rem;
          left: 50%;
          margin-left: -2.2rem;
      }
    }
    
`

const NextBrdr = styled.div`
  .defaultBox{
    border-radius: 1.2rem!important;
    img{
      height: 1.8rem;
      width: 1.8rem;
      margin-right: 1rem;
    }

  }
`

const Box = styled('div')`
    background: #F4F4FA;
    border-radius: 1.2rem;
    .balance{
      white-space: nowrap;
      margin-right: 3rem;
        font-family: "Roboto-Light"!important;
        color: #A6A6B7;
    }
`

const PriceTips = styled('div')`
    margin-top: 2rem;
    font-size: 1.4rem;
     font-family: "Roboto-Light"!important;
    font-weight: 300;
    color: #D52473;
    line-height: 1.6rem;
`


export default function TokenBuy(props) {
    const { show ,closeTokenBuy, fund } = props;

    const [pairAddress, setPairAddress] = useState()
    const [fundAmount, setFundAmount] = useState()
    const [tokenAmount, setTokenAmount] = useState()

    const handleInputChange = (e) =>{
        const { name, value } = e.target
        switch (name) {
            case "pairAddress":
                setPairAddress(value)
                break
            case "fundAmount": 
                setFundAmount(Number(value))
                break
            case "tokenAmount": 
                setTokenAmount(Number(value))
                break
            default:
                return
        }
    }

    const handleClose = () => {
        closeTokenBuy()
    }

    const clickBuyToken = () => {
      
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >Token Buy</span>
        </div>
       <div>
                <div className="label">Pair Address</div>
                <div className="inputBrdr">
                    <Input
                        placeholder='Please enter'
                        name='pairAddress'
                        value={pairAddress}
                        autoComplete="off"
                        onChange={(e)=>handleInputChange(e)}
                    />
                </div>
           <div className="label">{fund && fund.symbol}</div>
                <Box>
                    <div className="inputBrdr">
                        <Input
                            placeholder='Please enter'
                            name='fundAmount'
                            value={fundAmount}
                            autoComplete="off"
                            type="number"
                            onChange={(e)=>handleInputChange(e)}
                        />
                        <div className="balance">Balance: 100000 {fund && fund.symbol}</div>

                </div>

                </Box>
                <div className="label pr">ETH <img src={Arrow} alt=""/></div>
           <Box>
               <div className="inputBrdr">
                   <Input
                       placeholder='Please enter'
                       name='tokenAmount'
                       value={tokenAmount}
                       autoComplete="off"
                       type="number"
                       onChange={(e)=>handleInputChange(e)}
                   />
                   <div className="balance">Balance: 10 ETH</div>

               </div>

           </Box>

                <PriceTips>Price: 100 USDT per ETH</PriceTips>
                <NextBrdr >
                    <Button 
                      type="primary" 
                      className="defaultBox" style={{ width: '100%', marginTop: '3rem' }} 
                      disabled={tokenAmount < 0 || fundAmount < 0 || !pairAddress}
                      onClick={clickBuyToken}
                    >
                        <img src={Cart} alt=""/>Buy Now
                    </Button>
                </NextBrdr>
            </div>


    </ModalBox>
}
