import { forwardRef, useEffect, useState, useImperativeHandle } from "react";
import styled from "styled-components";
import { useWeb3 } from "../../api/contracts";

const EstimateTime = styled('div')`
    background: #FFFCF7;
    border-radius: 1.2rem;
    padding:1.8rem 2.5rem;
    margin-top: 1.1rem;
    font-size: 1.8rem;
    font-family: "Roboto-Light";
    font-weight: 300;
    color: #FFA224;
    line-height: 2.4rem;
`

const Estimate = forwardRef((props, ref) =>{
    const { state } = useWeb3();
    const { web3Instance } = state;

    const [height, setHeight] = useState(0)
    const [blockTime, setBlockTime] = useState(0)
    const [eheight, setEheight] = useState(0)

    const getCurrentData = async()=>{
        const c = 500
        const height = await web3Instance.getBlockNumber()
        setHeight(height)
        const currentBlock = await web3Instance.getBlock(height)
        const block = await web3Instance.getBlock(height-c)
        const speed = Math.round((currentBlock.timestamp - block.timestamp) / c)
        setBlockTime(speed)

        if (props.time) {
            const eheight = height + Math.round(speed * (props.time - new Date().getTime())/1000)
            setEheight(eheight)
        } else {
            setEheight(0)
        }
    }

    useEffect(()=>{
        getCurrentData()
    }, [])

    useEffect(()=>{
        getCurrentData()
    }, [props.time])

    useImperativeHandle(ref, () => ({
        getEstimateHeight: ()=>{
            return eheight
        },
        getCurrentHeight: ()=>{
            return height
        }
    }));


    return (
        <EstimateTime>
            <div>Current Height : {height}</div>
            <div>Block time : {blockTime}s</div>
            {!!eheight && <div>Estime height : {eheight}</div>}
        </EstimateTime>
    )
})

export default Estimate