const YourInvest = ({mintToken, deposit})=>{
    return (
        <Section>
            <div className="titleTop">Invest info</div>
            <Ul className='font-light'>
                <LI>
                    <div className='title'>Your Token</div>
                    <div className='value1'>{mintToken.userBalance}</div>
                    <div className='symbol'>{mintToken.symbol}</div>
                </LI>
                <LI>
                    <div className='title'>Your Deposit</div>
                    <div className='value1'>{mintToken.userBalance}</div>
                    <div className='symbol'>{deposit.symbol}</div>
                </LI>
            </Ul>
        </Section>
    )
}

const VenturePeriod = [
    "",
    "Raise Period",
    "GPInvestPeriod",
    "LPInvestPeriod",
    "RaiseFailPeriod",
    "FreezingPeriod",
    "SettlementPeriod"
]
export default function Venture(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { Account, venturecontract, maincontract, whoAmI, web3Instance, stockcontract} = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [buyShow, setBuyShow] = useState(false);
    const [raiseGp, setRaiseGp] = useState(false);
    const [raiseLp, setRaiseLp] = useState(false);
    const [whitelist, setWhitelist] = useState(false);
    const [deposit, setDeposit] = useState(false);
    const [withdraw, setWithdraw] = useState(false);
    const [confirmShow, setConfirmShow] = useState(false);
    const [confirmTips, setConfirmTips] = useState('');
    const [tokenBuy, setTokenbuy] = useState(false);

    const [period, setPeriod] = useState()

    const [mintToken, setMintToken] = useState({})
    const [fund, setFund] = useState({})

    const [yourDeposit, setYourDeposit] = useState({})
    const [manageFee, setManageFee] = useState(0)
    const [bonus, setBonus] = useState(0)


    const [tokenInfo, setTokenInfo] = useState({
        contract: '',
        id: ''
    })

    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);

    useEffect(async()=>{
        if (!id || !mainConnect) return
    }, [id, mainConnect])


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(()=>{
        if (!venturecontract) return
    }, [venturecontract])


    const handleInput = (e) => {
        const { name } = e.target;
        tokenInfo[name] = e.target.value
        setTokenInfo(tokenInfo)
    }

    const clickSearch = ()=>{

    }
    const clickTokenBuy = ()=>{
        setTokenbuy(true)
    }

    const CloseTokenBuy = () =>{
        setTokenbuy(false)
    }
    const clickRaise = async ()=>{
        // period===1 => gp
        if (period === 1) {
            setRaiseGp(true)
            return
        } 
        if (!venturecontract) return
        if (period === 2 ) {
            const gpStopHeight = parseInt((await venturecontract.GpSubStopTime()).toString())
            const currentHeight = await web3Instance.getBlockNumber()
            console.log(`gpStopHeight: ${gpStopHeight}, currentHeight: ${currentHeight}`)
            if (currentHeight >= gpStopHeight) {
                // config LP raised
                setRaiseLp(true)
            }
            return
        }
        if (period === 2) {
            // LP raised period
        }
    }
    const closeGPRaise = ()=>{
        setRaiseGp(false)
    }
    const closeLPRaise = ()=>{
        setRaiseLp(false)
    }
    
    const clickWhitelist = ()=>{
        setWhitelist(true)
    }
    const closeWhitelist = ()=>{
        setWhitelist(false)
    }
    const clickDeposit = ()=>{
        setDeposit(true);
    }

    const closeDeposit = ()=>{
        setDeposit(false)
    }
    const clickWithdraw = ()=>{
        setWithdraw(true);
    }
    const clickTokenBuyModal = ()=>{
        
    }
    const closeTokenBuyModal = ()=>{
        
    }

    const closeWithdraw = ()=>{
        setWithdraw(false)
    }
    const clickCloseVenture = ()=>{
        setConfirmShow(true);
        setConfirmTips(1);
    }
    const CloseShow = ()=>{
        setConfirmShow(false);
    }

    const clickRefund = ()=>{
        setConfirmShow(true);
        setConfirmTips(2);
    }

    const updateMintToken = (token)=>{
        setMintToken(token)
    }


    useEffect(()=>{
        if (!venturecontract) return
        const getVenturePeriod = async()=>{
            try {
                const period = await venturecontract.getVenturePeriod()
                console.log("[current period]", period, VenturePeriod[period+1])
                setPeriod(period+1)
            } catch (error) {
                console.error('getVenturePeriod failed:', error)
            }
        }
        getVenturePeriod()
    }, [venturecontract])
    

    useEffect(()=>{
        if (!venturecontract) return
        
        const getfund = async()=>{
            try {
                const {raised, goal, address} = await api.venture.getFundInfo(venturecontract)
                const fundContract = await api.erc20.getContract(web3Instance, address)
                const symbol = await fundContract.symbol()
                const deci = await fundContract.decimals()
                setFund({
                    raised: ethers.utils.formatUnits(raised, deci).toString(), 
                    goal: ethers.utils.formatUnits(goal, deci).toString(),
                    symbol, address
                })
            } catch (error) {
                console.error("getfund failed", error)
            }
        }
        getfund()
    }, [venturecontract])

    useEffect(()=>{
        if (!venturecontract) return 
        const getOthers = async()=>{
            try {
                const fee = await venturecontract.ManageFeePercent()
                setManageFee(fee.toString())
            } catch (error) {
                console.error('get ManageFeePercent failed', error)
            }
            try {
                const bonus = await venturecontract.totalGPBonusPercent()
                setBonus(bonus.toString())
            } catch (error) {
                console.error('get totalGPBonusPercent failed', error)
            }
        }
        getOthers()
    }, [venturecontract])

    useEffect(()=>{
        if (!fund || !fund.symbol || mintToken.userBalance) return
        const getYourDeposit = async()=>{
            try {
                // TODO
                setYourDeposit({
                    // value: mintToken.userBalance,
                    symbol: fund.symbol
                })
            } catch (error) {
                
            }
        }
        getYourDeposit()
    }, [fund, mintToken])

    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip>{errorTips}</Tip>
            </Modal>


            <div className='container'>
                <Left history={props.history} id={props.match.params.id} updateMintToken={updateMintToken} />
                <Section>
                    <div className="titleTop">Raise info</div>
                    <Period className='font-light'>{period && VenturePeriod[period]}</Period>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Mint Token</div>
                            <div className='value1'>{mintToken.balance}</div>
                            <div className='symbol'>{mintToken.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Fund Type</div>
                            {fund.address && <div className='value1'>{fund.address.slice(0, 6)}...{fund.address.slice(-6)}</div>}
                            <div className='symbol'>{fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Raised Fund</div>
                            <div className='value1'>{fund.raised}</div>
                            <div className='symbol'>{fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Raised Goal</div>
                            <div className='value1'>{fund.goal}</div>
                            <div className='symbol'>{fund.symbol}</div>
                        </LI>
                    </Ul>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Manage Fee</div>
                            <div className='value2'>{manageFee}%</div>
                        </LI>
                        <LI>
                            <div className='title'>GP Bouns</div>
                            <div className='value2'>{bonus}%</div>
                        </LI>
                    </Ul>
                </Section>
                <YourInvest mintToken={mintToken} deposit={yourDeposit}></YourInvest>
                <Section>
                    <div className="titleTop">Investment</div>
                    <InvestSection className='font-light'>
                        <div>
                            <div className="input-title">Token contract</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.contract}
                                    name='contract'
                                    checked={tokenInfo.contract}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div>
                            <div className="input-title">Token id</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.id}
                                    name='id'
                                    checked={tokenInfo.id}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                   
                        <div className='btn-search '>
                            <NButton className='light' onClick={() => clickSearch()}>
                                <img src={Search} alt="" />
                                Search
                            </NButton>
                        </div>
                    </InvestSection>

                </Section>
                <BuyModal show={buyShow} CloseTokenBuy={CloseTokenBuy} />
                <Section>
                    <div className="titleTop">Token buy</div>
                    <NButton className='light font-light' onClick={() => clickTokenBuy()}>Click me to buy</NButton>
                </Section>
                <Section>
                    <TokenBuy show={tokenBuy} closeTokenBuy={()=>CloseTokenBuy()} venturecontract={venturecontract} />
                    {
                        period !== 4 && (
                            <>
                                <RaiseGps show={raiseGp} closeRaise={()=>closeGPRaise()} fund={fund}/>
                                <RaiseLps show={raiseLp} closeRaise={()=>closeLPRaise()} fund={fund}/>
                                { isOwner && <NButton onClick={() => clickRaise()}>Raise</NButton> }
                                <Whitelist show={whitelist} closeRaise={()=>closeWhitelist()}/>
                                {/*<NButton onClick={() => clickWhitelist()}>Lp White List</NButton>*/}


                                <Deposit 
                                    show={deposit} 
                                    closeDeposit={()=>closeDeposit()} 
                                    showContent={true} 
                                    fund={fund} 
                                    mintToken={mintToken}
                                />
                                <NButton onClick={() => clickDeposit()}>Deposit</NButton>


                                <Withdraw show={withdraw} closeWithdraw={()=>closeWithdraw()} />
                                <NButton className='pirmary' onClick={() => clickWithdraw()}>Withdraw</NButton>


                                <NButton className='pirmary' onClick={() => clickCloseVenture()}>Close venture</NButton>

                            </>
                        )
                    }
                    {
                        period === 4 && <NButton className='pirmary' onClick={() => clickRefund()}>Refund</NButton>
                    }
                    <ConfirmBox show={confirmShow} handleClose={()=>CloseShow()} tips={confirmTips} />
                </Section>
            </div>
        </div>
    )
}
